// HOME SEVEN FORM
.cdx-form-7 {
	.form-group {
		position: relative;
		display: flex;
		align-items: stretch;
		border: 1px solid rgba(16, 18, 33, 0.4);
		background: #fff;
		padding: 5px;
		transition: all 0.3s ease;

		&.form-cta-7 {
			background: #fff;
			border-color: transparent;
			box-shadow: 0px 6px 6px 0px rgba(16, 18, 33, 0.14);
		}

		&:focus-within {
			border: 1px solid #0ea9fa;
		}

		@media #{$sm} {
			flex-direction: column;
		}
	}

	.input-wrapper {
		flex: 1;
		position: relative;
	}

	.form-control {
		font-weight: 500;
		color: var(--dark);
		width: 100%;
		padding-inline-start: 35px;
		border: none;
		background: transparent;
		height: 50px;

		&::placeholder {
			color: var(--dark);
			opacity: 0.6;
			font-weight: 500;
		}

		&:focus,
		&:active {
			outline: none;
			border: none;
			box-shadow: unset;
		}

		@media #{$sm} {
			text-align: center;
			padding-inline: 15px;
		}
	}
}
