/*=========================
    FOOTER CSS TWO
==========================*/
.cdx-footer-area-2 {
	background: linear-gradient(
		90deg,
		#8b1e56 -6%,
		rgba(55, 112, 255, 0.4) 54%,
		rgba(39, 118, 29, 0.4) 86%
	);
	.footer-btm {
		background-color: var(--dark-2);
	}
	.cdx-info-link {
		display: inline-flex;
		align-items: center;
		gap: 10px;
		color: var(--white);
		font-size: 14px;
		font-weight: 400;
		transition: var(--transition);
		&:hover {
			color: var(--primary);
		}
	}
}

// cta-wrap-2
.cta-wrap-2 {
	padding: 60px 160px 0px 60px;
	box-shadow: 0px 10px 10px 0px rgba(5, 6, 13, 0.3);
	transform: translate(0px, -217px);
	position: relative;
	z-index: 9;
	background-color: var(--dark-2);
	margin-bottom: -11%;
	@media #{$xl} {
		margin-bottom: -25%;
	}
	@media #{$sm} {
		padding: 60px 15px 0px 15px;
		transform: translate(0px, -130px);
	}

	&::before {
		position: absolute;
		top: 0px;
		left: 0px;
		width: 100%;
		height: 100%;
		background: linear-gradient(
			-90deg,
			#8b1e56 -6%,
			rgba(55, 112, 255, 0.4) 54%,
			rgba(39, 118, 29, 0.4) 86%
		);
		content: '';
		z-index: -1;
	}
	.cdx-right {
		text-align: right;
		@media #{$md} {
			text-align: unset;
		}
	}
	.cdx-section-title-2 {
		font-size: 60px;
		@media #{$xl} {
			font-size: 40px;
		}
	}
	.cdx-btn-filled-3 {
		@media #{$sm} {
			font-size: 12px;
		}
	}
}

// social-style-2
.cdx-social-style-2 {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	gap: 20px;
	.cdx-social-link {
		width: 46px;
		height: 46px;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: var(--dark-2);
		border: 1px solid rgba(255, 255, 255, 0.1);
		box-shadow: 0px 10px 10px 0px rgba(5, 6, 13, 0.3);
		transition: var(--transition);
		&:hover {
			background-color: var(--yellow-2);
		}
	}
}

.cdx-contact-wrap {
	display: flex;
	flex-direction: column;
	gap: 20px;
	@media #{$sm} {
		gap: 10px;
	}
	.cdx-item {
		display: flex;
		align-items: center;
		gap: 10px;

		.cdx-icon {
			width: 40px;
			border: 1px solid var(--gray-9);
			height: 40px;
			flex-shrink: 0;
			display: flex;
			align-items: center;
			justify-content: center;
			@media #{$sm} {
				width: 30px;
				height: 30px;
			}
		}

		.cdx-text {
			color: rgba(255, 255, 255, 0.7);
			font-size: 16px;
			font-weight: 500;
			@media #{$sm} {
				font-size: 14px;
			}
		}

		&:hover {
			.cdx-text {
				color: var(--white);
			}
		}
	}
}

// footer-btm-area-2
.footer-btm-area-2 {
	background: #141728;
	box-shadow: 0px 10px 30px 0px rgba(255, 255, 255, 0.1) inset;
	padding: 26px 15px;

	.footer-btm-wrap {
		display: flex;
		align-items: center;
		justify-content: space-between;

		@media #{$sm} {
			flex-direction: column;
			gap: 5px;
			text-align: center;
		}
	}
}
