/*=========================
    FAQ CSS Two
==========================*/
.cdx-faq-area-2 {
	.faq-shape {
		position: absolute;
		bottom: -18px;
		left: -18px;
		z-index: -1;
		@media #{$sm} {
			left: 0px;
		}
	}
	.cdx-accordion {
		padding-right: 100px;
		@media #{$md} {
			padding-right: 0px;
		}
	}
}

// Custom accordion css
.cdx-accordion {
	.cdx-accordion-item {
		border-bottom: 1px solid rgba(255, 255, 255, 0.2);
		padding: 25px 0px;
		transition: 0.8s ease-out;
		&.active {
			.cdx-accordion-body {
				overflow: visible;
			}
			.cdx-plus {
				opacity: 0;
			}
		}
	}

	.cdx-accordion-heading {
		cursor: pointer;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding-bottom: 8px;
		transition: 0.8s ease-out;
	}

	.cdx-accordion-body {
		height: 0;
		overflow: hidden;
		transition: height 0.5s ease-out;
	}

	.cdx-title {
		color: rgba(255, 255, 255, 0.6);
		font-size: 22px;
		font-weight: 500;
		line-height: 1.3;
	}

	.cdx-accordion-content {
		color: rgba(16, 18, 33, 0.5);
		font-size: 18px;
		font-weight: 500;

		&.dark {
			color: var(--white);
			opacity: 0.6;
			font-weight: 400;
		}
		@media #{$sm} {
			font-size: 16px;
		}
	}

	.cdx-icon-wrap {
		position: relative;
	}

	.cdx-minus {
		position: absolute;
		top: 12px;
		left: 0;
	}
}
