.cdx-form-style-4 {
	.form-wrap {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 30px;

		@media #{$lg} {
			gap: 20px;
		}

		@media #{$sm} {
			grid-template-columns: 1fr;
		}
	}
	.form-group {
		&.span-2 {
			grid-column: 2 span;

			@media #{$sm} {
				grid-column: 1 span;
			}
		}

		&.check-box {
			border: 1px solid rgba(255, 255, 255, 0.1);
			padding: 20px;
			display: flex;
			align-items: center;
			gap: 10px;
			justify-content: space-between;
			@media #{$sm} {
				flex-direction: column;
				align-items: stretch;
			}
		}

		.form-label {
			color: #fff;
			font-size: 16px;
			font-weight: 700;
			opacity: 0.5;
			margin-bottom: 0;

			@media #{$lg} {
				font-size: 14px;
			}
		}
	}

	.form-input {
		border: 1px solid rgba(255, 255, 255, 0.1);
		height: 100%;
		background: rgba(29, 39, 57, 0);
		color: var(--white-5);
		font-size: 14px;
		font-weight: 500;
		width: 100%;
		padding: 25px 15px;
		transition: 0.3s;
		&:focus {
			border: 1px solid var(--green);
		}

		&.sm {
			padding: 15px 15px;
		}
		@media #{$lg} {
			padding: 20px 12px;
		}
	}

	.checkbox-wrapper-33 .visuallyhidden {
		border: 0;
		clip: rect(0 0 0 0);
		height: 1px;
		margin: -1px;
		overflow: hidden;
		padding: 0;
		position: absolute;
		width: 1px;
	}

	.checkbox-wrapper-33 .checkbox {
		display: flex;
		align-items: center;
		justify-content: flex-start;
	}

	.checkbox-wrapper-33 .checkbox + .checkbox {
		margin-top: 1.2em;
	}

	.checkbox-wrapper-33 .checkbox-symbol {
		display: inline-block;
		display: flex;
		margin-right: calc(1.2em * 0.7);
		border: 1px solid var(--dark-5);
		position: relative;
		border-radius: 0.1em;
		width: 1.5em;
		height: 1.5em;
		transition: box-shadow 0.4s cubic-bezier(0.11, 0.29, 0.18, 0.98),
			background-color 0.4s;
		box-shadow: 0 0 0 0 rgba(0, 36, 37, 0.1);
		@media #{$xs} {
			width: 2.5em;
		}
	}

	.checkbox-wrapper-33 .checkbox-symbol:after {
		content: '';
		position: absolute;
		top: 0.5em;
		left: 0.5em;
		width: 0.25em;
		height: 0.25em;
		background-color: rgba(0, 4, 37, 0.356);
		opacity: 0;
		border-radius: 3em;
		transform: scale(1);
		transform-origin: 50% 50%;
	}

	.checkbox-wrapper-33 .checkbox .icon-checkbox {
		width: 1em;
		height: 1em;
		margin: auto;
		fill: none;
		stroke-width: 3;
		stroke: var(--green);
		stroke-linecap: round;
		stroke-linejoin: round;
		stroke-miterlimit: 10;
		color: var(--dark-5);
		display: inline-block;
	}

	.checkbox-wrapper-33 .checkbox .icon-checkbox path {
		transition: stroke-dashoffset 0.2s ease-in;
		stroke-dasharray: 30px, 31px;
		stroke-dashoffset: 31px;
	}

	.checkbox-wrapper-33 .checkbox__textwrapper {
		margin: 0;
	}

	.checkbox-wrapper-33 .checkbox-trigger:checked + .checkbox-symbol:after {
		-webkit-animation: ripple-33 1.5s cubic-bezier(0.11, 0.29, 0.18, 0.98);
		animation: ripple-33 1.5s cubic-bezier(0.11, 0.29, 0.18, 0.98);
	}

	.checkbox-wrapper-33
		.checkbox-trigger:checked
		+ .checkbox-symbol
		.icon-checkbox
		path {
		transition: stroke-dashoffset 0.4s cubic-bezier(0.11, 0.29, 0.18, 0.98);
		stroke-dashoffset: 0px;
	}

	.checkbox-wrapper-33 .checkbox-trigger:focus + .checkbox-symbol {
		box-shadow: 0 0 0 0.25em rgba(0, 4, 37, 0.356);
	}

	@-webkit-keyframes ripple-33 {
		from {
			transform: scale(0);
			opacity: 1;
		}

		to {
			opacity: 0;
			transform: scale(20);
		}
	}

	@keyframes ripple-33 {
		from {
			transform: scale(0);
			opacity: 1;
		}

		to {
			opacity: 0;
			transform: scale(20);
		}
	}
}
