/*=========================
    TECHNOLOGY CSS Two
==========================*/
.cdx-technology-area-2 {
  
   
}


.cdx-tecnology-item {
    transition: var(--transition);
    margin-bottom: 30px;
    &:hover {
         .cdx-thumb-wrap {
            &::before {
                opacity: 1;
                visibility: visible;
            }
         }
         .cdx-details  {
            opacity: 1;
            visibility: visible;
            bottom: 24px;
         }
    }
    .cdx-thumb-wrap {
        position: relative;
        &::before {
            position: absolute;
            bottom: 0px;
            left: 0%;
            width: 100%;
            height: 100%;
            background: linear-gradient(180deg, rgba(16, 18, 33, 0.00) 0%, #101221 100%);
            content: "";
            opacity: 0;
            visibility: hidden;
            transition: var(--transition);
        }
    }
    .cdx-details {
        position: absolute;
        bottom: 30px;
        left: 20px;
        opacity: 0;
        visibility: hidden;
        transition: var(--transition);
    }
    .cdx-subtitle {
        color: var(--dark);  
        font-size: 18px; 
        font-weight: 500; 
        background-color: var(--white);
        margin-bottom: 6px;
        padding: 7px 18px;
        display: block;
        width: fit-content;
    }
    .cdx-title {
        padding: 7px 18px;
        color: var(--dark);  
        font-size: 24px; 
        font-weight: 500; 
        display: inline-block;
        background-color: var(--white);
    }

}