.btn-gradient-border {
	color: var(--white);
	font-size: 15px;
	font-weight: 500;
	padding: 16px 30px 17px;
	display: inline-block;
	background: linear-gradient(
		85deg,
		rgba(227, 180, 25, 0.2) 0%,
		rgba(35, 225, 115, 0.2) 50.5%,
		rgba(39, 161, 237, 0.2) 100%
	);
	border: 1px solid;
	border-image-slice: 1;
	border-width: 1px;
	border-image-source: linear-gradient(
		85deg,
		#e3b419 0%,
		#23e173 50.5%,
		#27a1ed 100%
	);
	transition: var(--transition);
	&:hover {
		color: var(--white);
		background: linear-gradient(
			-85deg,
			rgba(227, 180, 25, 0.2) 0%,
			rgba(35, 225, 115, 0.2) 50.5%,
			rgba(39, 161, 237, 0.2) 100%
		);
		box-shadow: 0 4px 15px 0 rgba(49, 196, 190, 0.75);
	}
}

.btn-arrow {
	@include flex-center(inline-flex);
	gap: 10px;
	color: var(--white);
	font-size: 15px;
	font-weight: 500;
	letter-spacing: -0.3px;
	position: relative;
	transition: var(--transition);
	.btn-icon {
		transition: var(--transition);
	}
	&::before {
		position: absolute;
		bottom: -5px;
		width: 0%;
		left: 0%;
		height: 2px;
		background: linear-gradient(85deg, #e3b419 0%, #23e173 50.5%, #27a1ed 100%);
		content: '';
		transition: var(--transition);
	}
	&:hover {
		color: var(--white);
		.btn-icon {
			transform: rotate(45deg);
		}
	}
	&:hover::before {
		width: 100%;
	}
}

.cdx-btn-filled {
	color: var(--dark-2);
	font-size: 15px;
	font-weight: 500;
	display: inline-block;
	padding: 16px 32px;
	background-color: var(--yellow-2);
	position: relative;
	z-index: 1;
	overflow: hidden;
	white-space: nowrap;
}
