// cdx-hover-slide
.cdx-hover-slide {
	&::before {
		position: absolute;
		top: 0%;
		left: 0%;
		width: 0%;
		height: 50%;
		background-color: var(--white);
		content: '';
		transition: all ease 0.4s;
		z-index: -1;
	}
	&::after {
		position: absolute;
		bottom: 0%;
		right: 0%;
		width: 0%;
		height: 50%;
		background-color: var(--white);
		content: '';
		transition: all ease 0.4s;
		z-index: -1;
	}
	&:hover {
		color: var(--dark);
		&::before {
			width: 100%;
		}
		&::after {
			width: 100%;
		}
	}
}

