// cdx-hover-alernate
.cdx-hover-alernate {
	transition: all ease 0.5s;
	&::before {
		position: absolute;
		top: 0%;
		left: 0%;
		width: 25%;
		height: 0%;
		background-color: var(--primary);
		content: '';
		transition: all ease 0.5s;
		z-index: -1;
	}
	&::after {
		position: absolute;
		bottom: 0%;
		right: 0%;
		width: 25%;
		height: 0%;
		background-color: var(--primary);
		content: '';
		transition: all ease 0.4s;
		z-index: -1;
	}
	.cdx-btn-icon {
		transition: var(--transition);
	}
	.cdx-btn-effect {
		&::before {
			position: absolute;
			top: 0%;
			left: 50%;
			width: 25%;
			height: 0%;
			background-color: var(--primary);
			content: '';
			transition: all ease 0.3s;
			z-index: -1;
		}
		&::after {
			position: absolute;
			bottom: 0%;
			right: 50%;
			width: 25%;
			height: 0%;
			background-color: var(--primary);
			content: '';
			transition: all ease 0.2s;
			z-index: -1;
		}
	}
	&:hover {
		color: var(--dark);
		&::before {
			height: 100%;
		}
		&::after {
			height: 100%;
		}
		.cdx-btn-icon {
			filter: brightness(0);
		}
		.cdx-btn-effect {
			&::before {
				height: 100%;
			}
			&::after {
				height: 100%;
			}
		}
	}
}

// cdx-hover-corners
.cdx-hover-corners {
