		height: 0%;
		background-color: var(--white);
		content: '';
		transition: all ease 1.5s;
		z-index: -1;
	}
	.cdx-btn-effect {
		&::before {
			position: absolute;
			bottom: 0%;
			left: 0%;
			width: 0%;
			height: 0%;
			background-color: var(--white);
			content: '';
			transition: all ease 1.5s;
			z-index: -1;
		}
		&::after {
			position: absolute;
			bottom: 0%;
			right: 0%;
			width: 0%;
			height: 0%;
			background-color: var(--white);
			content: '';
			transition: all ease 1.5s;
