// cdx-btn-video
.cdx-btn-video {
	position: relative;
	z-index: 1;
	display: inline-flex;
	align-items: center;
	gap: 10px;
	padding-right: 20px;
	transition: var(--transition);
	&:hover {
		.cdx-video-text {
			opacity: 1;
			visibility: visible;
			transform: translateX(-0px);
		}
		.cdx-video-icon-wrap {
			box-shadow: rgba(255, 255, 255, 0.3) 0px 0px 0px 0px;
			border: 1px solid rgba(16, 18, 33, 0.2);
		}
	}

	.cdx-video-icon-wrap {
		@include flex-center(inline-flex);
		width: 50px;
		height: 50px;
		border: 1px solid var(--white);
		background-color: var(--white);
		border-radius: 50%;
		box-shadow: rgba(255, 255, 255, 0.3) 0px 0px 0px 10px;
		transition: var(--transition);
		position: relative;
		z-index: 9;
	}
	.cdx-video-text {
		color: var(--dark);
		font-size: 14px;
		font-weight: 700;
		letter-spacing: -0.28px;
		opacity: 0;
		visibility: hidden;
		transition: var(--transition);
		transform: translateX(-30px);
		display: block;
		border-radius: 100px;
		background-color: var(--white);
		text-align: center;
		padding: 15px 20px 15px 60px;
		position: absolute;
		width: 170px;
		top: 0px;
		left: 0%;
		z-index: -1;
	}
}

