/* FOR HOME SEVEN */
%base-button-seven {
	@include flex-center(inline-flex);
	gap: 8px;
	position: relative;
	line-height: 1;
	font-weight: 500;
	overflow: hidden;
	padding: 17px 28px;
	text-align: center;
	transition: all 0.3s ease;

	&:hover {
		cursor: pointer;
	}

	&:disabled {
		cursor: default;
		pointer-events: none;
		user-select: none;
	}
}

%before-base-button-seven {
	color: var(--dark);
	z-index: 1;

	&::after {
		content: '';
		display: block;
		position: absolute;
		inset: 0;
		background: linear-gradient(180deg, #0ea9fa -1.09%, #1552b4 98.91%);
		transition: all 0.3s ease;
		z-index: -1;
		opacity: 0;
	}

	&:hover {
		color: #fff;
		box-shadow: 0px 4px 6px 0px rgba(16, 18, 33, 0.24);
		border-color: #0ea9fa;

		&::after {
			opacity: 1;
		}
	}
}

.btn {
	&-primary-seven {
		@extend %base-button-seven;
		color: #fff;
		background: linear-gradient(180deg, #0ea9fa -1.09%, #1552b4 98.91%);
		filter: drop-shadow(0px 4px 6px rgba(16, 18, 33, 0.24));
	}

	&-secondary-seven {
		@extend %base-button-seven;
		background: rgba(16, 18, 33, 0.06);
		@extend %before-base-button-seven;
	}

	&-outline-seven {
		@extend %base-button-seven;
		border: 1px solid rgba(16, 18, 33, 0.2);
		background: rgba(255, 255, 255, 0);
		@extend %before-base-button-seven;
	}
}

// FOR HOME EIGHT
.btn {
	&-primary-eight {
		@extend %base-button-seven;
		color: var(--dark);
		background: #55ed6c;
		z-index: 1;

		&.cdx-hover-alernate {
			&::before,
			&::after {
				background: #1e9261;
			}

			.cdx-btn-effect {
				&::before,
				&::after {
					background: #1e9261;
				}
			}
		}

		&:hover {
			color: #fff;
		}
	}

	&-secondary-eight {
		@extend %base-button-seven;
		color: #fff;
		background: #1e9261;
		z-index: 1;

		&.cdx-hover-alernate {
			&::before,
			&::after {
				background: #55ed6c;
			}

			.cdx-btn-effect {
				&::before,
				&::after {
					background: #55ed6c;
				}
			}
		}

		&:hover {
			color: var(--dark);
		}
	}
}

// FOR HOME NINE
%base-button-nine {
	@include flex-center(inline-flex, 18px);
	font-size: 16px;
	font-weight: 500;
	line-height: 1;
	letter-spacing: -0.32px;
	position: relative;
	padding: 8px 20px;
	transition: all 0.3s ease;
	flex-shrink: 0;
	min-height: 40px;
	text-align: center;
	z-index: 1;

	&::before {
		content: '';
		position: absolute;
		inset: 0;
		z-index: -1;
	}

	.inline-btn-arrow {
		@include flex-center;
		@include square-element(30px, 50%);
		background: transparent;
		position: relative;
		z-index: 1;

		&::before {
			content: '';
			@include position-center();
			left: -10px;
			@include square-element(10px, 50%);
			transform: translate(0, -50%) scale(0);
			transform-origin: right;
			transition: all 0.3s ease;
		}

		&::after {
			content: '';
			position: absolute;
			inset: 0;
			border-radius: 50%;
			padding: 2px;
			-webkit-mask: linear-gradient(#fff 0 0) content-box,
				linear-gradient(#fff 0 0);
			mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
			-webkit-mask-composite: xor;
			mask-composite: exclude;
			transition: all 0.3s ease;
			z-index: -1;
		}

		.icon-arrow {
			position: relative;
			z-index: 1;
		}

		@media #{$sm} {
			display: none !important;
		}
	}

	&:hover {
		.inline-btn-arrow {
			&::before {
				transform: translate(0, -50%) scale(1);
			}

			&::after {
				transform: scale(0);
				transform-origin: left;
			}
		}
	}
}

.btn {
	&-primary-nine {
		@extend %base-button-nine;
		color: #fff;
		box-shadow: 0px 4px 6px 0px rgba(16, 18, 33, 0.24);

		&::before {
			background: linear-gradient(95deg, #4038b7 0%, #e24a70 100%);
		}

		.inline-btn-arrow {
			&::before {
				background: #fff;
			}

			&::after {
				background: linear-gradient(90deg, transparent 40%, #fff 100%);
			}
		}

		&:hover {
			color: #fff;

			&::before {
				background: var(--dark);
			}
		}
	}

	&-secondary-nine {
		@extend %base-button-nine;
		color: #fff;

		&::before {
			background: var(--dark);
		}

		.inline-btn-arrow {
			&::before {
				background: #fff;
			}

			&::after {
				background: linear-gradient(90deg, transparent 40%, #fff 100%);
			}
		}

		&:hover {
			color: #fff;

			&::before {
				background: linear-gradient(95deg, #4038b7 0%, #e24a70 100%);
			}
		}
	}

	&-pricing-nine {
		@extend .btn-secondary-nine;

		&:hover {
			color: #fff;
			box-shadow: 0px 4px 6px 0px rgba(16, 18, 33, 0.24);
		}
	}

	&-tertiary-nine {
		@extend %base-button-nine;
		color: var(--dark);

		.inline-btn-arrow {
			&::before {
				background: var(--dark);
			}

			&::after {
				background: linear-gradient(90deg, transparent 40%, var(--dark) 100%);
			}
		}

		&:hover {
			color: var(--dark);
		}
	}
}

